
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
html, body {
  width: 100%;
  height: 100%;
  margin: 0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  width: 100%;
  height: 100%;
}
.homePage {
  background-size: 100%;
  background-repeat: no-repeat;
  background-image: url(/ying-resume/static/img/bg.1a33344.jpg);
  width: 100%;
  height: 100%;
  color: #fff;
}
.homePage > .itemOne {
    padding-top: 180px;
    font-size: 60px;
}
.homePage > .itemOne span:nth-child(1) {
      letter-spacing: 5px;
}
.homePage > .itemOne span:nth-child(2) {
      margin-left: 20px;
}
.homePage > .itemTwo {
    margin-top: 40px;
    padding-top: 40px;
    font-size: 28px;
    position: relative;
}
.homePage > .itemTwo span:nth-child(1) {
      letter-spacing: 5px;
}
.homePage > .itemTwo span:nth-child(2) {
      margin-left: 20px;
}
.homePage > .itemTwo:before {
    content: "";
    display: inline-block;
    position: absolute;
    left: 45%;
    right: 45%;
    top: 0;
    width: 10%;
    height: 1px;
    margin: auto;
    background-color: #fff;
}
.homePage > .itemThree {
    font-size: 20px;
    padding-top: 10px;
    letter-spacing: 1px;
}
.homePage > div {
    border-radius: 5px;
    -moz-border-radius: 5px;
    -webkit-border-radius: 5px;
}
